<template>
  <div class="spec-preview">
    <img :src="imgUrl" v-show="imgUrl" />
    <div class="event" @mousemove="moveImg"></div>
    <div class="big">
      <img :src="imgUrl" ref="bigImg" v-show="imgUrl" />
    </div>
    <div class="mask" ref="mask"></div>
  </div>
</template>

<script>
export default {
  name: "Zoom",
  props: ["skuDefaultImg"],
  data() {
    return {
      imgUrl: "",
    };
  },
  mounted() {
    this.$bus.$on("getDetailImgUrl", (imgUrl) => {
      this.imgUrl = imgUrl;
    });
  },
  watch: {
    skuDefaultImg(newval) {
      this.imgUrl = newval;
    },
  },
  methods: {
    moveImg(e) {
      const mask = this.$refs.mask;
      const bigImg = this.$refs.bigImg;
      // 鼠标的位置
      let mouse = {
        x: e.offsetX,
        y: e.offsetY,
      };
      //  遮罩的宽高
      let maskWh = {
        x: mask.offsetWidth,
        y: mask.offsetHeight,
      };
      //计算mask要移动的距离
      let maskMove = {
        x: mouse.x - maskWh.x / 2,
        y: mouse.y - maskWh.y / 2,
      };
      // 判断x临界值
      if (maskMove.x < 0) {
        maskMove.x = 0;
      } else if (maskMove.x > e.target.clientWidth - mask.offsetWidth) {
        maskMove.x = e.target.clientWidth - mask.offsetWidth;
      }
      // 判断y临界值
      if (maskMove.y < 0) {
        maskMove.y = 0;
      } else if (maskMove.y > e.target.clientHeight - mask.offsetHeight) {
        maskMove.y = e.target.clientHeight - mask.offsetHeight;
      }
      // 遮罩层移动
      mask.style.left = maskMove.x + "px";
      mask.style.top = maskMove.y + "px";
      // 大图移动
      bigImg.style.left = -maskMove.x * 2 + "px";
      bigImg.style.top = -maskMove.y * 2 + "px";
    },
  },
};
</script>

<style lang="less">
.spec-preview {
  position: relative;
  width: 400px;
  height: 400px;
  border: 1px solid #ccc;

  img {
    width: 100%;
    height: 100%;
  }

  .event {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 998;
  }

  .mask {
    width: 50%;
    height: 50%;
    background-color: rgba(0, 255, 0, 0.3);
    position: absolute;
    left: 0;
    top: 0;
    display: none;
  }

  .big {
    width: 100%;
    height: 100%;
    position: absolute;
    top: -1px;
    left: 100%;
    border: 1px solid #aaa;
    overflow: hidden;
    z-index: 998;
    display: none;
    background: white;

    img {
      width: 200%;
      max-width: 200%;
      height: 200%;
      position: absolute;
      left: 0;
      top: 0;
    }
  }

  .event:hover ~ .mask,
  .event:hover ~ .big {
    display: block;
  }
}
</style>